import React, { useState } from "react";
import "./index.scss";
import { Link, useLocation, useHistory } from "react-router-dom";

import { FaUserCircle } from "react-icons/fa";
import {
  CNavbar,
  CToggler,
  CNavbarBrand,
  CCollapse,
  CNavbarNav,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
  CContainer,
} from "@coreui/react";

import LogoPage from "../../../../assets/icons/logo-main.png";

const HeaderPage = (props) => {
  const location = useLocation();
  const history = useHistory();
  const [isOpen, setIsOpen] = useState(false);

  const { pathname } = location;

  const checkLogin = localStorage.getItem("isLogin");

  const onLogout = () => {
    localStorage.removeItem("isLogin");
    history.push("/login");
  };

  const handleViewProfile = () => {
    history.push("/profile");
  };

  return (
    <div className="headerPage">
      <CContainer>
        <CNavbar expandable="md" color="light" className=" navbar-light ">
          <CToggler
            className="navMobile"
            color="white"
            inNavbar
            onClick={() => setIsOpen(!isOpen)}
          />
          <CNavbarBrand>
            <Link to="/" className="logoPage">
              <img src={LogoPage} alt="" />
            </Link>
          </CNavbarBrand>
          <CCollapse show={isOpen} navbar>
            <CNavbarNav className="ml-auto">
              <Link to="/" className={`${pathname === "/" ? "active" : ""}`}>
                Home
              </Link>
              <Link
                to="/west-hollywood"
                className={`${pathname === "/west-hollywood" ? "active" : ""}`}
              >
                WEST HOLLYWOOD
              </Link>
              <Link
                to="/silver-lake"
                className={`${pathname === "/silver-lake" ? "active" : ""}`}
              >
                SILVER LAKE
              </Link>
              <Link
                to="/dashboard"
                className={`${pathname === "/dashboard" ? "active" : ""}`}
              >
                DASHBOARD
              </Link>
            </CNavbarNav>
            <CNavbarNav className="ml-auto">
              {checkLogin ? (
                <CDropdown className="userMenu" inNav>
                  <CDropdownToggle color="primary">
                    <img
                      width={35}
                      src="https://toppng.com/uploads/preview/roger-berry-avatar-placeholder-11562991561rbrfzlng6h.png"
                      alt=""
                    />
                    Username
                  </CDropdownToggle>
                  <CDropdownMenu placement="bottom-end">
                    <CDropdownItem onClick={handleViewProfile}>
                      Account
                    </CDropdownItem>
                    <CDropdownItem divider />
                    <CDropdownItem onClick={onLogout}>Log Out</CDropdownItem>
                  </CDropdownMenu>
                </CDropdown>
              ) : (
                <Link to="/login" className="btnLogin">
                  <FaUserCircle />
                  Log In
                </Link>
              )}
            </CNavbarNav>
          </CCollapse>
        </CNavbar>
      </CContainer>
    </div>
  );
};

export default HeaderPage;
